import React from 'react';
import Hands from './img/Hands.jpg'
import Work from './img/working.jpg'


const Inicio = props => {
    return (
        <div>
            <div id='about' className='container-fluid'>
                <div className='row'>
                    <div className='col-sm-8'>
                        <h2>SOBRE O CURSO</h2><br />
                        <h4>ESPECIALIZAÇÃO EM
                            GESTÃO E CONTROLE DA QUALIDADE busca a formação continuada de profissionais de
                            engenharia e áreas afins.</h4><br />
                        <p>O objetivo principal do curso de <strong>ESPECIALIZAÇÃO EM GESTÃO E CONTROLE DA
                            QUALIDADE</strong> é a formação continuada de profissionais para atender as demandas frente ao mercado pós
                            pandemia, tornando o aluno apto para atuar em qualquer setor industrial/empresarial ou público,
                            possibilitando que este possa entender seu papel para melhoria da qualidade em um processo, um produto
                            ou em um serviço. Para isso o curso deve conjugar conhecimentos técnicos e científicos a serem
                            repassados aos alunos a fim de que este possa visualizar fraquezas e oportunidades de melhorias,
                            otimizando um processo e/ou propondo inovação.</p>
                        <br /><button className='btn btn-default btn-lg'>INSCREVA-SE</button>
                    </div>
                    <div className='col-sm-4'>
                    <img src= {Work} alt = '' width='400' height='300'/>
                    </div>
                </div>
            </div>
            <div className='container-fluid bg-grey'>
                <div className='row'>
                    <div className='col-sm-4'>
                        <img src= {Hands} alt = '' width='400' height='300'/>
                    </div>
                    <div className='col-sm-8'>
                        <h2>CONCEPÇÃO DO PROGRAMA</h2><br />
                        <h4><strong>PÚBLICO ALVO: </strong> Graduados em Química, Física, Tecnologia de Alimentos, Engenharia Química, de Produção, de
                            Alimentos, de Bioprocessos, Mecânica, Civil e outras engenharias e áreas afins.</h4><br />
                        <p><strong>ASPECTOS FUNDAMENTAIS: </strong>O curso poderá ser ministrado em qualquer período do ano incluindo férias.O
                            curso ficará sob a responsabilidade acadêmica do Programa de Pós-Graduação em Engenharia de
                            Processos do Instituto de Tecnologia (ITEC) da UFPA e será ministrado por docentes da UFPA e
                            docentes especialistas de outras instituições.
                            <p><br />
                                Durante o curso o aluno verá: <br /></p>
                            Como identificar fraquezas e oportunidades em um processo;
                            Metodologias de avaliação de projetos;
                            Ferramentas da qualidade aplicadas às diversas áreas do conhecimento;
                            Sistemas de certificação;
                            Levantamento de dados e tratamento estatístico;
                            Aplicação de cartas de controle;
                            Ciclos de melhoria DMAIC e PDCA entre outros;
                            A filosofia Lean Manufacturing e suas métricas;
                            A metodologia seis sigma para melhoria da qualidade
                        </p>
                        <p><strong>CARGA HORÁRIA: </strong>450 Horas</p>
                        <p><strong>DURAÇÃO: </strong>18 Meses</p>
                        <p><strong>VAGAS: </strong>50 Vagas</p>
                    </div>
                </div>
            </div>
        </div >

    )
}
export default Inicio